import axios from 'axios'

const AXIOS = axios.create({
    baseURL: '/api',
    timeout: 10000,
    proxy: true
  });


export default {
    hello() {
        return AXIOS.get(`/hello`);
    },
    getUser(userId) {
        return AXIOS.get(`/user/` + userId);
    },
    createUser(firstName, lastName) {
        return AXIOS.post(`/user/` + firstName + '/' + lastName);
    },
    getSecured(user, password) {
        return AXIOS.get(`/secured/`,{
            auth: {
                username: user,
                password: password
            }});
    },
    getAtleta(token) {
        return AXIOS.post('/autenticar/atleta',{
                tokenStrava: token
            });            
    },
    login(token){
        return AXIOS.post('/atleta');
    },
    getVersao(){
        return AXIOS.get('/sistema/versao');
    }

}


