<template>
  <div class="login" align="center">
    
    <div class="loading" v-if="isLoading">
      <app-loading/>
    </div>

     <div class="boxLogin" v-if="!isLoading">
        <img src="./../assets/runner.png" class="logo">
        <form @submit.prevent="handleSubmit">
            <div class="form-group">
                <label for="username">Username</label>
                <input type="text" v-model="username" name="username" class="form-control" :class="{ 'is-invalid': submitted && !username }" />
                <div v-show="submitted && !username" class="invalid-feedback">Username is required</div>
            </div>
            <div class="form-group">
                <label htmlFor="password">Password</label>
                <input type="password" v-model="password" name="password" class="form-control" :class="{ 'is-invalid': submitted && !password }" />
                <div v-show="submitted && !password" class="invalid-feedback">Password is required</div>
            </div>
            <div class="form-group">
                <button class="btnLogin">Login</button>
            </div>
            <div class="form-group">
                <form @submit.prevent="callLogin()">
                  <b-btn type="submit" class="btnLoginStrava"/>
                </form>  
            </div>
        </form>
    </div>
  
  </div>
</template>


<script>
import axios from 'axios';
import api from "./backend-api";
import Loading from "./loading/Load.vue"
export default {
  name: 'login',

   data() {
      return {
        isLoading: false,
        username: '',
        password: '',
        submitted: false
      }
    },
  components: {
    'app-loading': Loading
  },
  methods: {
    callLogin() {
      location.href = "https://www.strava.com/oauth/authorize?client_id=36149&response_type=code&redirect_uri=http://localhost:8080&approval_prompt=force&scope=profile:write,activity:read_all"
    }
  },

   created: function () {
            this.data = this.$router.currentRoute.query;
            if (this.data.code != undefined  || this.data.code != null){
              this.isLoading = true;
              axios({
                      method: 'post',
                      url: 'https://www.strava.com/oauth/token',
                      headers: {
                                'Authorization': 'Bearer ' +  this.data.code,
                                'Content-Type': 'application/json'
                      }, 
                      data: {
                              "client_id" : 36149,
                              "client_secret" : "c45f33fb7c6d499516e21fb02340d777bf3c77fd",
                              "code" : this.data.code,
                              "grant_type" : "authorization_code"
                          }
                    })
            .then(response => {
              this.data = response.data;
              if(response.status == 200){
                  this.$store.dispatch('login', {token : this.data.access_token})
                  .then(() => {
                               this.$router.push('/principal')
                               this.isLoading = false;
                  })
                  .catch(error => {
                                    this.loginError = true;
                                    this.errors.push(error);
                                    this.error = true
                                    this.isLoading = false;
                  })
                }

            })
            }   
   }
}
</script>

